@import url(https://fonts.googleapis.com/css2?family=Lato&family=Jua&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Jua', sans-serif;
  -webkit-tap-highlight-color: transparent;
}

html, body {
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  background-color: #363636 !important;
  min-height: 100vh;
}

h1 {
  color: #fff;
  font-family: 'Jua', sans-serif !important;
  font-weight: normal;
  text-align: center;
}

